import React, {Component} from 'react';


export default class extends Component{
    constructor(props){
        super(props);
        this.state = {
            users:null
        };

    }
    async getInitialProps(){
        const res = await fetch('localhost:4000/user')
        const users = await res.json()
        console.log("This is users", users)
        return {users}        
    }



    async componentDidMount(){
        let users = this.getInitialProps()
        this.setState({
            users: users
        })
        console.log(this.props.users)
    }

    // renderTableHeader(){
    //     var myData = Object.assign({}, this.state.users[0]);
    //     let header = Object.keys(myData)

    //     return header.map((key,index)=>{

    //         return (
    //             <th key={index} >{key.toUpperCase()}</th>
    //         )
    //     })
    // }

    renderTableData(){
        if(!this.state.users) {
            return null;
        }
        return this.state.users.map((data,index)=>{
            const {id,name,email,yob,role}=data
            return (
                <tr key={id}>
                    <td>{id}</td>
                    <td>{name}</td>    
                    <td>{email}</td>
                    <td>{yob}</td>
                    <td>{role}</td>
                </tr>
            )
        })
    }

    render(){
        //console.log(this.state.users)
        return (
            <div>

                <h1 className="text-center">{this.state.name} List Table</h1>
                <table id="userData" className="table table-striped">
                    {/* <thead className="thead-dark">
                        <tr>
                            {this.renderTableHeader()}
                        </tr>
                    </thead> */}
                    <tbody>
                        {this.renderTableData()}
                    </tbody>
                </table>
            </div>
        )
    }
}

